<template>
	<Tabs value="message" class="tab">
		<Tab-pane label="我的消息" name="message" class="message-item">
			<Table size="large" highlight-row :columns="msgHeader" :data="useMsgList" no-data-text="暂无消息"></Table>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="$router.go(-1)">返回</i-button>
	</Tabs>
</template>
<script>
	export default {
		data () {
			return {
				msgHeader: [
					{
						type: 'index',
						title: '序号',
						width: '80px',
						align: 'center'
					},
					{
						title: '时间',
						key: 'initTime',
						align: 'center',
						render: (h, params) => {
							return h('span', new Date(params.row.initTime).Format('yyyy-MM-dd hh:mm'))
						}
					},
					{
						title: '类型',
						key: 'type',
						align: 'center',
						render: (h, params) => {
							let type = params.row.type;
							var typeName = '';
							switch (type) {
								case '0':
									typeName = '授信终审';
									break;
								case '1':
									typeName = '放款审批';
									break;
								case '2':
									typeName = '账务确认';
									break;
								case '3':
									typeName = '入库管理';
									break;
								case '4':
									typeName = '出库管理';
									break;
								case '5':
									typeName = '出入审批';
									break;
								default:
									typeName = '系统消息'

							}
							return h('span', typeName)
						}
					},
					{
						title: '提示',
						key: 'content',
						align: 'center'
					},
					{
						title: '状态',
						key: 'status',
						align: 'center',
						render: (h, params) => {
							return h('span', {
								class: {
									unread: params.row.status == '0'
								}
							}, params.row.status == 0 ? '未读' : '已读')
						}
					},
					{
						title: '操作',
						key: 'action',
						align: 'center',
						render: (h, params) => {
							var _this = this;
							return h('i-button', {
								props: {
									type: 'info',
									size: 'small',
								},
								style: {
									marginRight: '5px'
								},
								on: {
									click: () => {
										let n=params.row.type;
										let name;
										console.log(name)
										switch (n){
											case 1001://授信初审
												name='creditstart';
												break;
											case 1002://授信复审
												name='creditfinish';
												break;
											case 1003://放款初审
												name='loanstart';
												break;
											case 1004://放款复审
												name='loanongoing';
												break;
											case 1005://放款终审
												name='loanfinish';
												break;
											case 1006://账务确认
												name='loanconfirm';
												break;
											case 1080://金融专员
												name='director';
												break;
											case 1081://金融专员
												name='director';
												break;
											default:
												name='message';
												return;
										}
										_this.$router.push(name);
										_this.$store.dispatch('changeStatus');
									}
								}
							}, '查看')
						}
					}
				],
			}
		},
		created: function () {
			this.$store.dispatch('changeStatus');
		},
		computed: {
			useMsgList () {
				return this.$store.getters.useMsgList;
			}
		},
		methods: {}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 50px;
	}
	.message-item{
		padding-bottom: 30px;
	}
	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.layout-content .ivu-input-group-prepend {
		background-color: #20abff;
		border-color: #20abff;
		width: 80px;
	}

	.input-title {
		color: #fff;
		font-size: 14px;
	}

	.search-box {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		padding-top: 14px;
		padding-bottom: 15px;
		flex-wrap: wrap;
	}

	.search-item {
		flex: 1;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		padding-bottom: 15px;
		margin-right: 10px;
	}

	.item-title {
		width: 80px;
		height: 36px;
		font-size: 14px;
		line-height: 36px;
		text-align: center;
		color: #fff;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
	}

	.color-yellow {
		background-color: #f0b320;
	}

	.color-red {
		background-color: #ff7043;
	}

	.color-green {
		background-color: #28b779;
	}

	.unread {
		color: #f18c15;
	}

	.search {
		width: 100px;
	}

	.ivu-select-selection {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}

	.search-item .ivu-input {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}
</style>



